@import url("https://fonts.googleapis.com/css?family=Raleway&display=swap");
.About {
  color: white;
  margin-left: 75px;
  font-family: 'Raleway', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: calc(100% - 75px); }

.about-content {
  width: 95%;
  height: 80%;
  display: flex; }

.about-text-hold {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .about-text-hold article {
    display: flex;
    justify-content: center; }
  .about-text-hold h1 {
    font-size: 4rem;
    margin-bottom: 50px; }
    @media screen and (max-width: 1250px) {
      .about-text-hold h1 {
        font-size: 3rem; } }
  .about-text-hold p {
    font-size: 1.8rem;
    width: 80%;
    margin: 0;
    line-height: 35px; }
    @media screen and (max-width: 1250px) {
      .about-text-hold p {
        font-size: 1rem; } }

.about-image-hold {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

