@import url("https://fonts.googleapis.com/css?family=Raleway&display=swap");
.Portfolio {
  color: white;
  margin-left: 75px;
  font-family: 'Raleway', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: calc(100% - 75px); }

.portfolio-content {
  width: 95%;
  height: 80%;
  display: flex; }

.portfolio-item {
  height: 100%;
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.carousel-item {
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.carousel-image {
  height: 300px;
  margin: 25px 0; }

.carousel-image-label {
  margin-bottom: 25px; }

.portfolio-item-title {
  font-size: 3rem; }

.portfolio-item-list {
  list-style-type: disc;
  width: 80%;
  line-height: 25px;
  font-size: 1.3rem; }
