@import url("https://fonts.googleapis.com/css?family=Raleway&display=swap");
.Stack {
  color: white;
  margin-left: 75px;
  font-family: 'Raleway', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: calc(100% - 75px); }

.stack-content {
  width: 95%;
  height: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center; }
