@import url("https://fonts.googleapis.com/css?family=Raleway&display=swap");
.Contact {
  color: white;
  margin-left: 75px;
  font-family: 'Raleway', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: calc(100% - 75px); }

.contact-page {
  width: 95%;
  height: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center; }

.lets-chat {
  width: 100%;
  font-size: 5rem;
  text-align: center;
  margin-bottom: 25px; }

.contact-content-hold {
  width: 100%;
  height: 60%;
  display: flex;
  justify-content: space-between; }

.contact-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 55%; }

.contact-info-line {
  display: flex;
  align-items: center; }

.contact-info-icon {
  font-size: 3rem;
  margin-right: 25px; }

.contact-info-text {
  font-size: 1.8rem;
  text-decoration: none;
  color: white;
  padding-left: 25px;
  display: flex;
  align-items: center; }

.contact-form-hold {
  width: 45%;
  height: 100%; }
